import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next'; //  useTranslation
import './recommendedProduct.css';

const RecommendedProductPage = () => {
  const { t } = useTranslation(); //  
  const [products, setProducts] = useState([]); //   

  //  useEffect     
  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const response = await fetch('https://kwtmarkets.net/back/items'); //    
        const data = await response.json();
        console.log('Fetched Products:', data); //     console   
        if (data && Array.isArray(data)) {
          setProducts(data); //          
        } else {
          console.error('The data format is incorrect');
        }
      } catch (error) {
        console.error('Error fetching products:', error);
      }
    };

    fetchProducts(); //   fetchProducts
  }, []); //          

  return (
    <div className="recommended-product-page">
      <h1>{t('all_recommended_products')}</h1> {/*   */}
      <div className="product-grid">
        {products.length > 0 ? (
          products.map((product) => (
            <div
              className="product-card"
              key={product._id || product.id} //   id   
            >
              <Link to={`/product/${product._id || product.id}`}>
                <img
                  src={product.images[0] || 'default-image-url.jpg'} //       
                  alt={product.title || ''} // 
                  className="product-image"
                />
                <div className="product-details">
                  <h3>{product.title || t('no_name')}</h3> {/*   */}
                  <p>{product.description || t('no_description')}</p> {/*   */}
                  <p>{t('price')}: {product.price || t('no_price')}</p> {/*   */}
                </div>
              </Link>
            </div>
          ))
        ) : (
          <p>{t('loading_products')}</p> //   
        )}
      </div>
    </div>
  );
};

export default RecommendedProductPage;
